.about {
  margin-top: 2rem;
}

.description * {
  margin: 0;
  padding: 0;
  font-size: 1.4rem;
  font-weight: normal;
  line-height: 50px;
}

@import url("https://fonts.googleapis.com/css?family=Ubuntu");
@import url("https://fonts.googleapis.com/css?family=Ubuntu+Mono");

#container {
  font-family: Ubuntu;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 4rem;
  /* height: 100vh; */
}
#terminal {
  width: 85vw;
  height: 75vh;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.5);
}
#terminal__bar {
  display: flex;
  width: 100%;
  height: 30px;
  align-items: center;
  padding: 0 8px;
  box-sizing: border-box;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background: linear-gradient(#504b45 0%, #3c3b37 100%);
}
#bar__buttons {
  display: flex;
  align-items: center;
}
.bar__button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin-right: 5px;
  font-size: 8px;
  height: 12px;
  width: 12px;
  box-sizing: border-box;
  border: none;
  border-radius: 100%;
  background: linear-gradient(#7d7871 0%, #595953 100%);
  text-shadow: 0px 1px 0px rgba(255, 255, 255, 0.2);
  box-shadow: 0px 0px 1px 0px #41403a, 0px 1px 1px 0px #474642;
}
.bar__button:hover {
  cursor: pointer;
}
.bar__button:focus {
  outline: none;
}
#bar__button--exit {
  background: linear-gradient(#f37458 0%, #de4c12 100%);
  background-clip: padding-box;
}
#bar__user {
  color: #d5d0ce;
  margin-left: 6px;
  font-size: 14px;
  line-height: 15px;
}
#terminal__body {
  background: rgba(56, 4, 40, 0.9);
  font-family: "Ubuntu Mono";
  height: calc(100% - 30px);
  padding-top: 2px;
  margin-top: -1px;
  overflow-y: scroll;
}
#terminal__body * {
  font-size: 1rem;
}

#terminal__prompt {
  display: flex;
  margin: 8px 0;
}

#terminal__prompt.prompt__output {
  display: flex;
  flex-direction: column !important;
}

#terminal__prompt--user {
  color: #7eda28;
}
#terminal__prompt--location {
  color: #4878c0;
}
#terminal__prompt--bling {
  color: #dddddd;
}
#terminal__prompt--cursor {
  display: block;
  height: 17px;
  width: 8px;
  margin-left: 9px;
  animation: blink 1200ms linear infinite;
}

.command-output {
  color: ghostwhite;
}

.command-output .command-output-link {
  color: #61d6d6;
}

@keyframes blink {
  0% {
    background: #ffffff;
  }
  49% {
    background: #ffffff;
  }
  60% {
    background: transparent;
  }
  99% {
    background: transparent;
  }
  100% {
    background: #ffffff;
  }
}
@media (max-width: 600px) {
  #terminal {
    max-height: 90%;
    width: 90%;
  }
}
